.detail-page {
    width: 375px;
    overflow: hidden;
}

.steps-header {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin: 10px;
    text-align: center;
}

.step-num {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    margin: auto;
    border-radius: 1000px;
    width: 40px;
    height: 40px;
    background-color: #903FF6;
    color: white;
}

.scripts-wrapper {
    overflow-y: auto;
    height: 250px;
    margin-top: 20px;
}

.script:hover{
    border-radius: 10px;
    background-color: #eaeaea;
}

.video-detail {
    width: 375px;
    display: flex;
    flex-direction: column;
}

.video-wrapper {
    position: relative;
}

.video-wrapper>div {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.script-num {
    color: #ABABAB;
    margin-top: 10px;
}

.script-content-translated {
    color: #5F5F5F;
    margin-bottom: 10px;
}

.return-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    position: fixed;
    width: 40px;
    height: 40px;
    top: 700px;
    background: #000000;
    opacity: 0.3;
    border-radius: 1000px;

}

.detail-type-wrapper {
    height: 560px;
    overflow: hidden;
    margin-bottom: 15px;
}

.quiz-detail {
    background-color: #F7F7F7;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 28px 0px 40px;
    gap: 56px;

    position: absolute;
    width: 375px;
    left: calc(50% - 375px/2);
    top: 128px;


}

.quiz-progress {
    box-sizing: border-box;

    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 4px 8px;
    gap: 10px;

    width: 37px;
    height: 25px;

    border: 1px solid #919191;
    border-radius: 100px;

}

.quiz-question {

    width: 263px;
    height: 24px;

    font-family: 'Pretendard';
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;

    color: #1C1C1C;


}

.quiz-question-translated {
    width: 287px;
    height: 26px;

    font-family: 'Pretendard';
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 26px;
    text-align: center;

    color: #777777;

}

.quiz-question-empty {

    box-sizing: border-box;

    width: 283px;
    height: 32px;

    border-bottom: 1px solid #919191;


}

.quiz-btns {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    align-content: center;
    padding: 0px 20px;
    gap: 20px;

    width: 375px;
    height: 100px;
    cursor: pointer;

}

.quiz-btn {
    box-sizing: border-box;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 12px 18px;
    gap: 4px;

    height: 40px;

    background: #FFFFFF;
    border: 1px solid #C7C7C7;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.05);
    border-radius: 100px;

}


.scripts-wrapper::-webkit-scrollbar {
    width: 8px;
}

.scripts-wrapper::-webkit-scrollbar-track {
    background: #f0f0f0;
}

.scripts-wrapper::-webkit-scrollbar-thumb {
    background: #aaa;
    border-radius: 4px;
}

.scripts-wrapper::-webkit-scrollbar-thumb:hover {
    background: #999;
}